.welcomePage {
  width: 100%;
  .head {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: -1;
    height: 355px;
    .circle {
      position: absolute;
      border-radius: 50%;
    }
    .circle1 {
      top: 181px;
      right: 260px;
      width: 74px;
      height: 74px;
      background: rgba(159, 147, 255, 0.9);
      opacity: 0.45;
    }
    .circle2 {
      top: 134px;
      right: 213px;
      width: 168px;
      height: 168px;
      background: rgba(85, 68, 204, 0.6);
      opacity: 0.45;
    }
    .circle3 {
      top: 91px;
      right: 170px;
      width: 254px;
      height: 254px;
      background: rgba(85, 68, 204, 0.6);
      opacity: 0.45;
    }
    .circle4 {
      top: 44px;
      right: 123px;
      width: 348px;
      height: 348px;
      background: rgba(85, 68, 204, 0.6);
      opacity: 0.45;
    }
    .circle5 {
      top: -70px;
      right: -44px;
      width: 682px;
      height: 629px;
      background: #5544cc;
      opacity: 0.03;
    }
  }
  .headerContent {
    position: relative;

    padding: 63px 143px 49px 77px;
    background: #151515;
    opacity: 0.75;
  }
  .content {
    padding: 60px 80px;
  }
  .divide {
    width: 100%;
    height: 1px;
    margin: 48px 0;
  }
  .first-level-title {
    margin-bottom: 48px;
    font-size: 32px;
  }
  .first-level-title-base {
    font-size: 32px;
  }
  .second-level-title {
    margin-top: 13px;
    margin-bottom: 25px;
    font-size: 13px;
    line-height: 17px;
  }
  .ide-introduction {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    margin-top: 23px;
    margin-bottom: 15px;
    font-size: 18px;
    line-height: 17px;
    white-space: nowrap;
    img {
      width: 18px;
      margin-right: 6px;
      margin-left: 6px;
    }
  }
  .ide-resource {
    display: flex;
    flex-wrap: wrap;
    margin-left: -80px;
    .resource-card {
      position: relative;
      display: flex;
      width: 420px;
      height: 176px;
      margin-top: 30px;
      margin-left: 80px;
      padding: 17px 36px;
      overflow: hidden;
      background: rgba(32, 33, 35, 0.45);
      border-radius: 16px;
      cursor: pointer;
      &::before {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: -1;
        backdrop-filter: saturate(180%) blur(20px);
        content: '';
      }
      &:hover {
        background: rgba(16, 110, 190, 0.45);
        transition: all;
        .resource-card-bar {
          background: rgba(94, 16, 89, 0.45);
          box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.5);
        }
      }
      .resource-card-bar {
        position: absolute;
        top: 21px;
        bottom: 21px;
        left: 15px;
        width: 9px;
        background: rgba(18, 90, 150, 0.45);
        border-radius: 8px;
        box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.5);
      }
      .resource-card-intro {
        padding-top: 20px;
        text-align: center;
        .resource-card-title {
          width: 100%;
          margin-bottom: 9px;
          font-size: 17px;
          line-height: 22px;
        }
        .resource-card-subtitle {
          width: 100%;
          height: 17px;
          font-size: 13px;
          line-height: 17px;
        }
      }
      .resource-card-img-parent {
        display: flex;
        flex: 1;
        align-items: center;
        justify-content: center;
        & > img {
          width: 120px;
        }
      }
    }
  }
  .template-list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -80px;
    .template-card {
      display: flex;
      width: 420px;
      margin-bottom: 24px;
      margin-left: 80px;
      padding: 31px 24px 30px;
      background: #161717;
      border-radius: 16px;
      cursor: pointer;
      &:hover {
        .left-part {
          background: #133e62;
          .left-part-logo-white {
            display: none;
          }
          .left-part-logo {
            display: initial;
          }
        }
      }
      .left-part {
        flex-basis: 80px;
        width: 80px;
        height: 80px;
        margin-right: 16px;
        line-height: 80px;
        text-align: center;
        background: #133e62;
        border-radius: 50%;
        img {
          width: 45px;
          vertical-align: middle;
        }
        .left-part-logo {
          display: none;
        }
      }
      .right-part {
        flex: 1;
      }
      .top-part {
        display: flex;
        align-items: center;
        height: 27px;
        .template-name {
          flex: 1;
          overflow: hidden;
          font-weight: 600;
          font-size: 14px;
          text-transform: capitalize;
          text-overflow: ellipsis;
        }
        .template-version {
          width: 50px;
          font-style: oblique;
          text-align: right;
        }
      }
      .template-desc {
        display: -webkit-box;
        margin-top: 10px;
        overflow: hidden;
        color: #bebbb8;
        line-height: 17px;
        word-break: break-word;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
      }
    }
  }
  .project-list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -160px;
    .project-card {
      width: 268px;
      margin-bottom: 36px;
      margin-left: 160px;
      padding: 24px;
      border-radius: 2px;
      cursor: pointer;
      .project-window {
        width: 100%;
        height: 124px;
        margin-bottom: 9px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .project-card-bottom {
        display: flex;
        justify-content: space-between;
        .project-name {
          width: 50%;
          overflow: hidden;
          font-size: 13px;
          line-height: 17px;
          text-overflow: ellipsis;
        }
        .project-modify-date {
          width: 50%;
          font-size: 13px;
          line-height: 17px;
          text-align: right;
        }
      }
    }
  }
}
